import {appError, fnError, WinJSDec} from './APP/config';
import SET_REFRESH from './SET_REFRESH'

var name = 'SET_FLOOR';
var SET_FLOOR = (obj, fn = _ => {
}) => {
  if (typeof fn === 'function') {
    WinJSDec(_ => {
      SET_REFRESH();
      window.WinJSBridge.call('events', 'floor', obj, res => {
        var {code, response} = res;
        if (code == 0) {
          fn(response);
        } else {
          throw new Error(`[${name}] ${appError}`);
        }
      });
    });
  } else {
    throw new TypeError(`[${name}] ${fnError}`);
  }
};
export default SET_FLOOR;
